/* enterprises page */
.enterprises
  min-height: 100vh
  &__map
    position: relative
    .map-content
      position: absolute
      top: 0
      left: 0
      width: 100%
      height: 100%
    .container
      width: auto
  &__content
    padding: 30px 0
    position: relative
    z-index: 1
    max-width: 600px
  &__filter
    display: flex
    flex-wrap: wrap
    justify-content: space-between
    background-color: #fff
    padding: 0px 20px
    &__type
      display: inline-block
      margin: 10px 10px 10px 0
      background-color: #f9f9f9
      font-size: 15px
      line-height: 20px
      padding: 10px 15px
      text-transform: uppercase
      position: relative
      transition: color .3s
      overflow: hidden
      span
        position: relative
        z-index: 2
      &:before
        content: ''
        display: block
        width: 100%
        height: 100%
        position: absolute
        background-color: $accent-color
        top: 0
        left: 0
        transform: translateY(100%)
        transition: transform .3s
        z-index: 1
      &:hover
        color: #fff
        &:before
          transform: translateY(0)
      &:last-child
        margin-right: 0
  &__list
    margin: 20px 0
    .slick-arrow
      display: block
      width: 30px
      height: 30px
      font-size: 30px
      line-height: 30px
      margin: 0 auto
      cursor: pointer
      transition: color .3s
      &:hover
        color: $accent-color
      &:before
        transform: rotate(90deg) translate(25%,-35%)
    &__item
      margin: 10px 0
      cursor: pointer
      .item-content
        background-color: #fff
        display: flex
        padding: 20px
        position: relative
        &:before
          content: ''
          display: block
          width: 2px
          height: 0
          background-color: $accent-color
          position: absolute
          top: 0
          left: 0
          transition: height .3s
        .item-image
          flex: 0 0 auto
          width: 100%
          max-width: 160px
          img
            display: block
            width: 100%
            margin: 0
        .item-descr
          flex: 1 1 auto
          width: auto
          max-width: calc(100% - 160px)
          padding: 0 20px
          .enterprise-favorite
            display: block
            position: absolute
            top: 10px
            right: 10px
            color: rgba($txt-color, .5)
            transition: color .3s
            &:hover
              color: $accent-color
          .enterprise-name
            font-size: 18px
            line-height: 22px
            font-weight: 600
            transition: color .3s
          .enterprise-caregories
            display: block
            font-size: 15px
            line-height: 20px
            color: #999
            margin-top: 2px
          .enterprise-address
            display: block
            margin-top: 2px
          .enterprise-addinfo
            display: flex
            flex-wrap: wrap
            align-items: center
            margin-top: 10px
            .enterprise-rating
              display: flex
              flex: 0 0 auto
              margin-right: 15px
              .icon
                color: $accent-color
                display: block
                font-size: 16px
                line-height: 16px
                margin-right: 5px
                &:last-child
                  margin-right: 0
            .enterprise-mark
              display: inline-block
              font-weight: 600
              margin-right: 10px
            .enterprise-answers
              .answers-link
                position: relative
                &:after
                  content: ''
                  display: block
                  width: 0
                  height: 1px
                  position: absolute
                  bottom: -1px
                  left: 0
                  transition: width .3s
                  background-color: $accent-color
                &:hover
                  &:after
                    width: 100%
      &:hover, &.active
        .item-content
          &:before
            height: 100%
          .item-descr
            .enterprise-name
              color: $accent-color
